import axios from 'axios';

//const postBaseUrl = 'http://localhost:8080/api';
var postBaseUrl = 'http://nthustraydog.us-west-2.elasticbeanstalk.com/api';

export function getDog() {
    var dogID = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : '';

    //  console.log(dogID);
    var url = postBaseUrl + '/dogs';
    if (dogID) url += '?dogID=' + dogID;

    console.log('Making GET request to: ' + url);

    return axios.get(url).then(function (res) {
        if (res.status !== 200) throw new Error('Unexpected response code: ' + res.status);

        return res.data[0];
    });
}

export function sendmail(name, mail, date, time, dogname, comment) {
    var url = postBaseUrl + '/mails';
    console.log('Making mail request to: ' + url);

    return axios.post(url, {
        name: name,
        mail: mail,
        date: date,
        time: time,
        dogname: dogname,
        comment: comment
    }).then(function (res) {
        if (res.status != 200) throw new Error('Unexpected response code: ' + res.status);
    });
}

export function sendtell(name, mail, text, file) {
    var url = postBaseUrl + '/texts';
    console.log('Making text request to: ' + url);

    return axios.post(url, {
        name: name,
        mail: mail,
        text: text
    }).then(function (res) {
        if (res.status != 200) throw new Error('Unexpected response code: ' + res.status);
    });
}

export function createPost(barkerId, title, body) {
    var url = postBaseUrl + '/forum/post';

    console.log('Making POST request to: ' + url);

    return axios.post(url, {
        barkerId: barkerId,
        title: title,
        body: body
    }).then(function (res) {
        if (res.status !== 200) throw new Error('Unexpected response code: ' + res.status);
        return res.data;
    });
}

